
#contenedorsecundario{
	display: grid;
	grid-template-areas: 

	"seccion2 seccion2 seccion2"
	"seccion4 seccion4 seccion4" 
	"seccion5 seccion5 seccion5" 
	"seccion6 seccion6 seccion6" 
	"seccion1 seccion1 seccion1" 
	"seccion7 seccion7 seccion7" 
	"seccion3 seccion3 seccion3" 
	"seccion8 seccion8 seccion8";

	grid-template-rows: auto 600px 600px  600px 600px auto  700px 600px;
	grid-auto-columns:  1fr;     
	grid-gap: 10px;
	height: 100vh;   
	margin: 10px;  
	}

#seccion1{
	background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(63,27,92,0.9504844173997724) 35%, rgba(171,26,223,1) 100%);
    grid-area: seccion1;
	
}

#seccion2{
	/* background-color: rgb(16, 33, 190); */
    grid-area: seccion2;
	 
}

