.colorNeonSeccion5{
    position: relative;
    display: inline-block;
    padding: 15px 30px;
    color: #fff;
    letter-spacing: 4px;
    font-size: 24px;
    text-decoration: none;
    overflow: hidden;
    transition: 0.2s;
}

.colorNeonSeccion5 span{
    position: absolute;
    display: block;
}

#span1{
    top: 0;
    left: -100%;
    width: 100%;
    height: 10px;
    background: linear-gradient(90deg,transparent,#a945c7);
}

.colorNeonSeccion5:hover #span1{
    left: 100%;
    transition: 1s;
}

#span3{
    bottom: 0;
    right: -100%;
    width: 100%;
    height: 10px;
    background: linear-gradient(270deg,transparent,#a945c7);
}

.colorNeonSeccion5:hover #span3{
    right: 100%;
    transition: 1s;
    transition-delay: 0.5s;
}


#span2{
    top:-100%;
    right: 0;
    width: 10px;
    height: 100%;
    background: linear-gradient(180deg,transparent,#a945c7);
}

.colorNeonSeccion5:hover #span2{
    top: 100%;
    transition: 1s;
    transition-delay: 0.25s;
}


#span4{
    bottom:-100%;
    left: 0;
    width: 10px;
    height: 100%;
    background: linear-gradient(360deg,transparent,#a945c7);
}

.colorNeonSeccion5:hover #span4{
    bottom: 100%;
    transition: 1s;
    transition-delay: 0.75s;
}