#tituloSobreMi{
    padding-top: 30px;
    padding-bottom: 30px;
    text-align: center;
    font-family: 'Dancing Script', cursive;
    font-size: 60px;
    text-shadow: 0 0 5px rgb(47, 0, 255), 0 0 15px rgb(63, 60, 236), 0 0 30px rgb(50, 50, 205), 0 0 50px rgb(34, 0, 255);
    color: white; 
  }
  



/* /////////////// IMG 1 ////////// */

.img1{
    position: absolute; 
    background-image: url("../img/imgGusto1.jpg");
    width: 150px;
    height: 150px;    
    background-size: cover;
    top: 38%;
    left: 15%;   
    animation-name: mover1;
    animation-duration: 5s;    
    animation-iteration-count: infinite;
    animation-direction: alternate-reverse;
    transform: rotate(330deg);
    opacity: 0.1;
    border-radius: 10%;
    z-index:1;
}


@keyframes mover1 {
0%{
    transform: translate(-100px,0); 
}
}

#textoImagen1{  
    position: absolute;  
    font-size: 15px;
    bottom: 110%;
}

.img1:hover{
    opacity: 2;
    animation-play-state: paused;
}

/* .img1:active{
    transform: scale(2);
    color:#444;
    -webkit-transform:scale(2);transform:scale(2);
} */


