#tituloExperiencia{
    padding-top: 30px;
    padding-bottom: 60px;
    text-align: center;

    font-family: 'Dancing Script', cursive;
    font-size: 60px;
    text-shadow: 0 0 5px rgb(47, 0, 255), 0 0 15px rgb(63, 60, 236), 0 0 30px rgb(50, 50, 205), 0 0 50px rgb(34, 0, 255);
    /* font-family: 'Dancing Script', cursive;  */
    color: white; 

}


#contenedorExperiencias{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

/* ///////////////FONRECAR//////// */

#tituloFonrecar{     
       
    padding-left: 10px;      
    font-size: 25px;
    text-shadow: 0 0 15px rgb(247, 245, 245), 0 0 15px rgb(246, 244, 244),0 0 15px rgb(244, 244, 244), 0 0 15px rgb(248, 246, 246);
    color: white;
    font-weight: bold;
   
}

#fonrecar{     
    margin-top: 20px;
    margin-left: 50px;    
    margin-bottom: 40px;   
    transition-duration:3s;
    width: 150px;
    height: 30px;      
    padding: 10px;
    color: #fff;   
    font-size: 0px; 
    border-radius: 5px;
    background: #a945c7;
    box-shadow: 0 0 10px #a945c7, 0 0 40px #a945c7, 0 0 80px #a945c7;
}



#fonrecar:hover{    
    text-align:justify;
    font-size: 12px;
    height: 180px;    
    transition-duration: 2s;   
    background: #45c752;
    box-shadow: 0 0 10px #45c752, 0 0 40px #45c752, 0 0 80px #45c752;

}



.configuracionFonrecar{
    text-align:center;
    margin-top: 10px;
    font-family: 'Exo 2', sans-serif; 
    color: black;
    text-decoration: none;
    opacity: 0.3; 
}



#linkFonrecar{  
    transition-duration: 1s;
    text-decoration: none;
    color: black;
    opacity: 0.5;    
}

#linkFonrecar:hover{  
    transition-duration: 3s;
    font-size: 30px;
    color: white;  
    opacity: 5;   
 }

