#contenedorHeader{  
    display: flex;
    justify-content: center;
    padding: 30px;
}      

#contenedorTitulo{  
    margin-top: 60px;  
    border: 5px solid white;
    background-color: #444;
    box-shadow: inset 0 0 15px rgb(255, 0, 230), 0 0 15px rgb(255, 0, 230);
    text-align: center;   
    border-radius: 60px;
    height: 360px;
    width: 460px;

    background-image: url("../img/imagenFondoVieja.png");
  
    background-size: cover; 

}

.texto{
    margin: 0;
    font-size: 65px;
    text-shadow: 0 0 15px red, 0 0 15px red,0 0 15px red, 0 0 15px red;
    font-family: 'Dancing Script', cursive;     
    color: white; 
} 

#nombre{
    margin-top: 20px;
}



#textoSeparado{
 
    text-shadow: 0 0 15px rgb(38, 0, 255), 0 0 15px rgb(8, 0, 255), 0 0 15px rgb(4, 0, 255), 0 0 15px rgb(34, 0, 255);
    font-family: 'Dancing Script', cursive;     
    color: white; 
    font-weight: bold;
    font-size: 50px;
    word-spacing: -0.2em;
} 


.casiDa単ada{     
    color: white;
    animation: parpadear 2s infinite backwards;
    font-size: 50px;
    text-shadow: 0 0 15px rgb(38, 0, 255), 0 0 15px rgb(8, 0, 255), 0 0 15px rgb(4, 0, 255), 0 0 15px rgb(34, 0, 255);
    font-family: 'Dancing Script', cursive;      
    font-weight: bold;
}

.da単ado{
    font-family: 'Dancing Script', cursive;     
    font-size: 50px;
    font-weight: bold;
}



#textoDa単ado{     
    font-family: 'Dancing Script', cursive;     
    color: black; 
    font-size: 50px;
    font-family: 'Dancing Script', cursive;  
    font-weight: bold; 
} 

.encender2:hover{  
    color: white;
    text-shadow: 0 0 15px rgb(13, 0, 255), 0 0 15px rgb(21, 0, 255), 0 0 15px rgb(38, 0, 255), 0 0 15px rgb(38, 0, 255);
   
}

