.contenedorPrincipal{
    text-align: center;    
    padding-bottom: 50px;
}


.contenedorTareas{    
    display: flex;
    align-items: center;
    justify-content: center;    
}



.contenedorTareas{
    display: grid;
    grid-template-areas:

    "numero contenedor1    contenedor2   contenedor3";

    grid-template-rows: minmax(30px 30px 30px);
    grid-auto-columns:  100px 500px 100px 100px;  
}

body{
    background-color: rgb(199, 218, 211);
}

.titulos{
font-family:Verdana, Geneva, Tahoma, sans-serif;
font-size: 20px;
padding-top: 20px;
padding-bottom: 20px;
}

.Tituloprincipal{
padding-top: 50px;
padding-bottom: 20px;
}

.digitarTarea{ 
background-color: antiquewhite;
color: black;
font-family:Verdana, Geneva, Tahoma, sans-serif;
font-size: 20px;
padding: 10px;
border-radius: 10px;
}

.guardar{
color: black;
background-color: antiquewhite;
font-family:Verdana, Geneva, Tahoma, sans-serif;
font-size: 20px;
padding: 10px;
margin-left: 5px;
border-radius: 10px;
}

.numero{
grid-area: numero;
color: black;
font-family:Verdana, Geneva, Tahoma, sans-serif;
font-size: 20px;
margin-right: 10px;
}

.contenedor1{
grid-area: contenedor1;
background-color: antiquewhite;
color: black;
font-family:Verdana, Geneva, Tahoma, sans-serif;
font-size: 20px;
word-wrap: break-word; 
padding: 10px;
margin: 5px;
opacity: 0.6;
border-bottom: 2px solid #899d82;
}

.contenedor1Completado{    
text-decoration:line-through;
background-color: rgba(64, 65, 68, 0.176);
color: black;
font-family:Verdana, Geneva, Tahoma, sans-serif;
font-size: 20px;
word-wrap: break-word; 
padding: 20px;
margin: 5px;
opacity: 0.6;

}

.contenedor3{
    margin-left: 10px;
}

.eliminar{
background-color: brown;
color: wheat;
width: 60px;
height: 50px;
font-family:Verdana, Geneva, Tahoma, sans-serif;
font-size: 15px;
border-radius: 10%;
}

.actualizar{
background-color: rgb(159, 230, 27);
color: black;
width: 60px;
height: 50px;
font-family:Verdana, Geneva, Tahoma, sans-serif;
font-size: 15px;
border-radius: 10%;
}


#textoIndisponibilidadAT{
	display: none;
}

@media(max-width: 800px){  
    .contenedorPrincipal{
      display: none;
    }

    #textoIndisponibilidadAT{
		display: block;
		color: white;
		text-align: center;
		font-family: 'Abril Fatface', cursive;
		padding-top: 100px;
		padding-bottom: 100px;
		font-size: 20px;
		padding-left: 20px;
		padding-right: 20px;
	}


 }